


















.cover {
  height: 100vh;
  position: relative;
  overflow: hidden;
}
.cover__video {
    position: absolute;
    width: 100%;
    height: 100%;
    -o-object-fit: cover;
       object-fit: cover;
}
.cover__mask {
    position: absolute;
    background-color: #000;
    -webkit-transition: opacity 3s 1s;
    transition: opacity 3s 1s;
    width: 100%;
    height: 100%;
}
.cover__prompt {
    width: 100%;
    height: 100%;
    position: absolute;
}
.cover__prompt-to-bottom {
      width: 100px;
      height: 100px;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-direction: column;
              flex-direction: column;
      position: absolute;
      top: 20%;
      left: 50%;
      -webkit-transform: translate(-50%, -50%);
              transform: translate(-50%, -50%);
      border: 1.5px solid lightgrey;
      border-radius: 50%;
      color: lightgrey;
      font-size: 2.4rem;
      line-height: 32px;
      -webkit-box-sizing: border-box;
              box-sizing: border-box;
      -webkit-animation: prompt-to-bottom 3s 1s infinite;
              animation: prompt-to-bottom 3s 1s infinite;
}
@-webkit-keyframes prompt-to-bottom {
0% {
    opacity: 0;
}
20% {
    opacity: 0.5;
}
80% {
    opacity: 1;
}
90% {
    top: 50%;
    opacity: 0.5;
}
100% {
    top: 50%;
    opacity: 0;
}
}
@keyframes prompt-to-bottom {
0% {
    opacity: 0;
}
20% {
    opacity: 0.5;
}
80% {
    opacity: 1;
}
90% {
    top: 50%;
    opacity: 0.5;
}
100% {
    top: 50%;
    opacity: 0;
}
}
.cover__prompt-to-bottom--dashed {
        border-style: dashed;
        -webkit-animation-delay: 1.2s;
                animation-delay: 1.2s;
}
.cover__img {
    position: absolute;
    height: 100vh;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -webkit-transition: opacity 3s 1s;
    transition: opacity 3s 1s;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
.cover__img img {
      width: 20vh;
      height: auto;
      position: relative;
      -webkit-transition: all 1.6s;
      transition: all 1.6s;
}
.cover__title-wrapper {
    position: absolute;
    width: 100%;
    height: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.cover__title {
    position: relative;
    /*  for vertical center
     ** noted "align-items: center;" can make BOX-MODEL (so include "margin-top") center
     */
    margin-top: 14px;
    opacity: 0;
    right: 0;
    -webkit-transition: opacity 2s, right 2s 0.5s;
    transition: opacity 2s, right 2s 0.5s;
    color: #fff;
}
.cover__title-move {
      right: 25%;
}
.cover__title p {
      font-size: 3.2rem;
      margin-bottom: 2.4vh;
}
.cover__subtitle {
    position: relative;
    opacity: 0;
    -webkit-transform: translate(54px, 30px);
            transform: translate(54px, 30px);
    right: 25%;
    -webkit-transition: all 0.75s 1.75s;
    transition: all 0.75s 1.75s;
}
@media screen and (min-width: 769px) {
.cover__subtitle {
        -webkit-transform: translate(144px, -16px);
                transform: translate(144px, -16px);
}
}
.cover__subtitle-move {
      -webkit-transform: translate(54px, 22px);
              transform: translate(54px, 22px);
      opacity: 1;
}
@media screen and (min-width: 769px) {
.cover__subtitle-move {
          -webkit-transform: translate(128px, -16px);
                  transform: translate(128px, -16px);
}
}
.cover__subtitle h2 {
      color: #fff;
      font-size: 1.4rem;
      letter-spacing: 4px;
}
